:root {
  --bg: #0b1220;
  --bg-2: #0f1a30;
  --text: #eaf2ff;
  --muted: #a9b6d3;
  --card: rgba(255,255,255,0.06);
  --card-2: rgba(255,255,255,0.08);
  --border: rgba(255,255,255,0.12);
  --shadow: rgba(0, 0, 0, 0.35);

  --accent: #22c1f1; /* default (blue) */
  --accent-2: #60a5fa;
  --accent-soft: rgba(96,165,250,0.22);

  --radius: 14px;

  --mx: 50vw; /* mouse x */
  --my: 50vh; /* mouse y */
}

/* consistent inter-section gap used for several sections */
:root {
  --section-gap: clamp(48px, 6.5vh, 120px);
}

/* Green theme (toggle) */
[data-theme="green"] {
  --accent: #10b981;
  --accent-2: #34d399;
  --accent-soft: rgba(16,185,129,0.22);
}

* { box-sizing: border-box; }
html, body { margin: 0; padding: 0; }
body {
  font-family: Inter, system-ui, -apple-system, Segoe UI, Roboto, "Helvetica Neue", Arial, "Noto Sans", "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji", sans-serif;
  color: var(--text);
  background: radial-gradient(1200px circle at 10% -10%, #162341, transparent 35%) ,
              radial-gradient(1000px circle at 100% 10%, #0f2036, transparent 30%),
              linear-gradient(180deg, var(--bg), var(--bg-2));
  line-height: 1.6;
  overflow-x: hidden;
}

/* Mouse-follow light */
.bg-light {
  position: fixed;
  inset: 0;
  pointer-events: none;
  background:
    radial-gradient(450px circle at var(--mx) var(--my), rgba(96,165,250,0.18), transparent 60%),
    radial-gradient(650px circle at calc(var(--mx) + 80px) calc(var(--my) + 60px), var(--accent-soft), transparent 70%);
  z-index: 0;
  transition: background 120ms linear;
}

.wrap {
  max-width: 1360px;
  margin: 0 auto;
  padding: 0 20px;
}

/* make the .wrap inside .hero a two-column grid: left content + right visual */
.hero > .wrap {
  display: grid;
  grid-template-columns: 1.2fr 0.8fr;
  gap: 32px;
  align-items: center;
}

.nav-row {
  
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 64px;
}

/* Navbar horizontal line and sticky header */
header.nav {
  border-bottom: 1px solid var(--border);
  background: linear-gradient(180deg, rgba(10,16,32,0.6), rgba(10,16,32,0.4));
  position: sticky;
  top: 0;
  z-index: 80;
  transition: background 220ms ease, border-color 220ms ease, box-shadow 220ms ease;
}

/* header appearance when scrolled past hero */
header.nav.scrolled {
  background: linear-gradient(180deg, rgba(10,16,32,0.98), rgba(10,16,32,0.96));
  border-bottom-color: rgba(255,255,255,0.04);
  box-shadow: 0 6px 30px rgba(3,6,13,0.45);
  backdrop-filter: blur(6px);
}

/* Mobile menu button */
.menu-btn { display: none; appearance: none; border: 0; background: transparent; padding: 6px; cursor: pointer; }
.menu-btn .hamburger { display: inline-block; width: 22px; height: 2px; background: var(--text); position: relative; border-radius: 2px; }
.menu-btn .hamburger::before, .menu-btn .hamburger::after {
  content: ""; position: absolute; left: 0; width: 22px; height: 2px; background: var(--text); border-radius: 2px; transition: transform 180ms ease, opacity 180ms ease;
}
.menu-btn .hamburger::before { top: -7px; }
.menu-btn .hamburger::after { top: 7px; }
.menu-btn.open .hamburger { background: transparent; }
.menu-btn.open .hamburger::before { transform: translateY(7px) rotate(45deg); }
.menu-btn.open .hamburger::after { transform: translateY(-7px) rotate(-45deg); }

@media (max-width: 720px) {
  /* show hamburger */
  .menu-btn { display: inline-flex; align-items: center; justify-content: center; position: absolute; right: 16px; top: 50%; transform: translateY(-50%); z-index: 120; }

  /* hide the desktop nav and show mobile popup when open */
  header.nav nav { display: none; }
  header.nav.menu-open nav {
    display: block; position: absolute; right: 12px; top: calc(100% + 8px);
    background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01));
    border: 1px solid var(--border); padding: 12px; border-radius: 12px; box-shadow: 0 12px 40px rgba(0,0,0,0.35);
    min-width: 180px; z-index: 115;
  }
  header.nav.menu-open nav ul { display: flex; flex-direction: column; gap: 8px; }
  header.nav.menu-open nav a { display: block; padding: 8px 10px; }

  /* ensure brand and wrap spacing fit small screens */
  header.nav .wrap { padding-left: 20px; padding-right: 64px; }
}

/* Make hero wrap stack more cleanly on smaller viewports */
@media (max-width: 960px) {
  .hero > .wrap { display: grid; grid-template-columns: 1fr; gap: 18px; align-items: center; padding: 20px; }
  .hero { padding-top: 48px; }
  .portrait-wrapper { margin: 0 auto; }
}

/* place brand/logo at the left corner of the header */
header.nav .brand {
  position: absolute;
  left: 20px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 90;
}

/* ensure header content respects the left logo by adding left padding to wrap inside header */
header.nav .wrap {
  padding-left: 96px; /* adjust if logo size changes */
}

@media (max-width: 720px) {
  /* on small screens keep brand in flow */
  header.nav .brand { position: static; transform: none; }
  header.nav .wrap { padding-left: 20px; }
}

/* place primary nav on the right corner for wide screens */
header.nav nav {
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 90;
}

@media (max-width: 720px) {
  /* on small screens keep nav in normal flow below logo */
  header.nav nav { position: static; transform: none; margin-top: 12px; }
}

.brand {
  display: flex; align-items: center; gap: 12px;
  font-weight: 800; letter-spacing: 0.2px;
}

.brand .logo {
  width: 36px; height: 36px; display: grid; place-items: center;
  border-radius: 12px;
  background: linear-gradient(135deg, var(--accent), var(--accent-2));
  color: #0a1020; font-weight: 900;
  box-shadow: 0 8px 18px var(--shadow);
}

nav ul { display: flex; gap: 18px; list-style: none; margin: 0; padding: 0; }
nav a {
  text-decoration: none; color: var(--text);
  font-weight: 500; opacity: 0.9;
  padding: 6px 10px; border-radius: 8px;
}
nav a:hover { background: var(--card); }
.actions { display: flex; align-items: center; gap: 10px; }

.btn {
  appearance: none; border: 1px solid var(--border);
  color: var(--text);
  background: var(--card);
  border-radius: 10px;
  padding: 10px 14px; font-weight: 600;
  cursor: pointer;
  transition: transform 0.15s ease, background 0.15s ease, border-color 0.15s ease, box-shadow 0.15s ease;
}
.btn:hover { background: var(--card-2); transform: translateY(-1px); }
.btn:active { transform: translateY(0); }
.btn.primary {
  background: linear-gradient(135deg, var(--accent), var(--accent-2));
  color: #0a1020; border-color: transparent;
  box-shadow: 0 8px 24px rgba(32,144,255,0.24);
}
.btn.primary:hover {
  filter: brightness(1.03);
}
.btn.ghost {
  background: transparent; border-color: var(--accent-soft); color: var(--text);
}

main { position: relative; z-index: 1; }

section {
  padding: 72px 0;
  scroll-margin-top: 80px;
}

/* About section spacing: reduced to make About visually smaller while keeping global spacing elsewhere */
section#about {
  /* reduced top padding so About sits closer to the hero */
  padding-top: clamp(40px, 6vh, 72px);
  /* tighten bottom padding to bring Skills closer */
  padding-bottom: clamp(18px, 3vh, 36px);
}

section#skills {
  /* reduce vertical padding so Skills section is more compact */
  padding-top: clamp(12px, 2.5vh, 28px);
  padding-bottom: clamp(12px, 2.5vh, 28px);
}

/* Apply the same gap to UI/UX, Education, Activities (Other working experience), Contact and Footer */
section#uiux,
section#education,
section#activities,
section#contact,
footer {
  padding-top: calc(var(--section-gap) / 2);
  padding-bottom: calc(var(--section-gap) / 2);
}

/* Hero */
.hero {
  /* make hero cover full viewport and keep content centered */
  min-height: 90vh;
  padding: 0; /* remove default section padding so background fills edge-to-edge */
  /* center .wrap both vertically and horizontally on wide screens */
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.title {
  font-size: clamp(34px, 5vw, 56px);
  line-height: 1.05; font-weight: 900;
  margin: 0 0 14px;
  letter-spacing: -0.5px;
}
.sub {
  font-size: clamp(16px, 2.4vw, 22px);
  color: var(--muted);
  margin: 0 0 16px;
}
.tagline {
  font-size: 16px; color: #c8d6f9; opacity: 0.95;
  max-width: 820px;
  margin-bottom: 22px;
}
.cta { display: flex; gap: 10px; flex-wrap: wrap; margin: 18px 0 10px;}
.contact-row {
  display: flex; gap: 14px; flex-wrap: wrap; margin-top: 12px; color: var(--muted);
  font-size: 14px;
}
.contact-pill {
  display: inline-flex; align-items: center; gap: 8px;
  padding: 8px 12px; border: 1px solid var(--border);
  background: rgba(255,255,255,0.04); border-radius: 999px;
}
.contact-pill a { color: var(--text); text-decoration: none; }

/* Social links in hero (LinkedIn / Behance / GitHub / Email) */
.hero-socials {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
  margin-top: 12px;
}
.social-link {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  border-radius: 999px;
  border: 1px solid var(--border);
  background: rgba(255,255,255,0.03);
  color: var(--text);
  text-decoration: none;
  font-weight: 600;
  transition: background 0.15s ease, transform 0.12s ease, box-shadow 0.12s ease;
}
.social-link .i { width: 18px; height: 18px; border-radius: 6px; }
.social-link:hover { background: var(--card-2); transform: translateY(-2px); box-shadow: 0 8px 20px rgba(0,0,0,0.2); }
.social-link:active { transform: translateY(0); }
@media (max-width: 960px) {
  .hero-socials { justify-content: center; }
}
.stat {
  display: inline-flex; align-items: center; gap: 8px;
  padding: 8px 12px; border-radius: 10px; background: rgba(0,0,0,0.18);
  border: 1px dashed var(--accent-soft); color: #cfe8ff;
  margin-top: 14px; font-weight: 600;
}

.hero-visual {
  position: relative;
  min-height: clamp(280px, 40vh, 520px);
  display: grid; place-items: center;
}

/* Portrait styles for hero */
.portrait-container { display: flex; align-items: center; justify-content: center; }
.portrait-wrapper {
  /* enlarged responsive portrait size: min 140px, preferred 20vw, max 260px */
  width: clamp(140px, 20vw, 260px);
  height: clamp(140px, 20vw, 260px);
  border-radius: 9999px;
  overflow: hidden;
  position: relative;
  display: inline-block;
  box-shadow: 0 10px 30px rgba(0,0,0,0.35);
  border: 6px solid rgba(255,255,255,0.06);
  background: linear-gradient(135deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01));
}
.portrait-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}
.portrait-glow {
  position: absolute; inset: 0; pointer-events: none;
  box-shadow: 0 0 40px rgba(96,165,250,0.12) inset;
}

/* Skills bar pinned to bottom of the hero */
.hero {
  /* reserve more space at the bottom so the skills marquee doesn't crowd hero content */
  padding-bottom: clamp(140px, 14vh, 200px);
}
.hero-skills {
  position: absolute;
  left: 0; right: 0;
  bottom: clamp(18px, 4vh, 36px);
  display: flex;
  justify-content: center;
  gap: 10px;
  flex-wrap: wrap;
  padding: 0 20px;
  z-index: 30;
  pointer-events: auto;
}
.skill-chip {
  display: inline-flex; align-items: center; justify-content: center;
  padding: 8px 12px; border-radius: 0;
  background: rgba(255,255,255,0.03);
  color: var(--text); border: 1px solid rgba(255,255,255,0.04);
  font-weight: 700; font-size: 13px;
  transform: translateY(18px); opacity: 0;
  /* staggered entrance using --i */
  animation: slideUpFade 420ms cubic-bezier(.2,.8,.25,1) forwards;
  animation-delay: calc(var(--i, 0) * 80ms);
  /* horizontal float after entrance */
  animation-name: slideUpFade, floatX;
  animation-duration: 420ms, 4200ms;
  animation-timing-function: cubic-bezier(.2,.8,.25,1), ease-in-out;
  animation-fill-mode: forwards, both;
  animation-delay: calc(var(--i, 0) * 80ms), calc(var(--i, 0) * 80ms + 520ms);
}
.skill-chip:hover { transform: translateY(0) scale(1.03); box-shadow: 0 10px 28px rgba(0,0,0,0.28); animation-play-state: paused; }

@keyframes slideUpFade {
  to { transform: translateY(0); opacity: 1; }
}

@keyframes floatX {
  0% { transform: translateX(-6px); }
  50% { transform: translateX(6px); }
  100% { transform: translateX(-6px); }
}

/* Horizontal marquee for skills: continuous right-to-left scroll */
.skill-marquee {
  position: absolute;
  left: 0; right: 0;
  bottom: clamp(12px, 3.5vh, 28px);
  overflow: hidden;
  z-index: 30;
  pointer-events: none; /* let clicks pass to other elements */
}
.marquee-track {
  display: flex;
  gap: 12px;
  align-items: center;
  white-space: nowrap;
  will-change: transform;
  /* track contains two copies of the items; translateX moves by 50% to shift one copy */
  animation: marquee-left 18s linear infinite;
  padding: 6px 20px;
}
.marquee-item {
  pointer-events: auto; /* allow hover pause when user moves cursor over the chips */
  display: inline-flex; align-items: center; justify-content: center;
  padding: 8px 14px; border-radius: 0;
  background: rgba(255,255,255,0.03);
  color: var(--text); border: 1px solid rgba(255,255,255,0.04);
  font-weight: 700; font-size: 13px;
  flex: 0 0 auto;
}
.marquee-item:hover { transform: scale(1.04); box-shadow: 0 10px 28px rgba(0,0,0,0.28); animation-play-state: paused; }

/* small icon inside each marquee item */
.m-icon {
  display: inline-grid; place-items: center;
  width: 28px; height: 20px; min-width: 28px;
  margin-right: 8px; border-radius: 6px;
  font-size: 11px; font-weight: 900; color: #071022;
  background: linear-gradient(135deg, var(--accent), var(--accent-2));
  box-shadow: 0 6px 18px rgba(0,0,0,0.22);
}
.m-label { display: inline-block; }

/* image icon variant (SVG/PNG) */
.m-icon-img {
  width: 28px; height: 20px; min-width: 28px; object-fit: cover; margin-right: 8px; border-radius: 6px; display: inline-block;
}

@keyframes marquee-left {
  0% { transform: translateX(0%); }
  100% { transform: translateX(-50%); }
}
.glass {
  background: linear-gradient(180deg, rgba(255,255,255,0.06), rgba(255,255,255,0.03));
  border: 1px solid var(--border);
  border-radius: var(--radius);
  box-shadow: 0 12px 40px var(--shadow);
  padding: 20px;
}
.id-card {
  width: min(100%, 400px);
}
.id-card .name {
  display: flex; align-items: center; justify-content: space-between;
  border-bottom: 1px dashed var(--border);
  padding-bottom: 10px; margin-bottom: 12px;
}
.id-card .name h3 {
  margin: 0; font-size: 22px; letter-spacing: 0.3px;
}
.id-card .badge {
  font-family: "JetBrains Mono", monospace;
  font-size: 12px; padding: 6px 10px; border-radius: 22px;
  background: var(--card); border: 1px solid var(--accent-soft);
  color: #dff5ff;
}
.id-grid {
  display: grid; grid-template-columns: 1fr 1fr; gap: 10px;
  font-size: 14px; color: var(--muted);
}
.id-grid div { background: rgba(255,255,255,0.04); border: 1px solid var(--border); border-radius: 10px; padding: 10px; }
.id-grid strong { color: var(--text); font-weight: 700; display: block; margin-bottom: 4px; }

/* Floating tech bubbles around hero */
.float-layer {
  position: absolute; inset: 0; pointer-events: none;
}
.tech-bubble {
  position: absolute; transform: translate(-50%, -50%);
  padding: 10px 12px; border-radius: 999px;
  background: rgba(255,255,255,0.06);
  border: 1px solid var(--accent-soft);
  box-shadow: 0 8px 22px rgba(0,0,0,0.22);
  color: #dff8ff;
  font-weight: 700; font-size: 13px;
  text-shadow: 0 2px 6px rgba(0,0,0,0.35);
  user-select: none;
  white-space: nowrap;
}

/* hide floating tech bubbles (small labels around the hero portrait) */
.tech-bubble { display: none !important; }

/* Sections common */
h2.section-title {
  font-size: clamp(22px, 2.6vw, 28px);
  margin: 0 0 18px; letter-spacing: 0.2px;
}
.section-desc { color: var(--muted); margin: 0 0 22px; }

.grid {
  display: grid; gap: 16px;
}
.grid.cols-2 { grid-template-columns: repeat(2, minmax(0, 1fr)); }
.grid.cols-3 { grid-template-columns: repeat(3, minmax(0, 1fr)); }
.grid.cols-4 { grid-template-columns: repeat(4, minmax(0, 1fr)); }
@media (max-width: 960px) {
  .hero { grid-template-columns: 1fr; align-items: flex-start; padding-top: 48px; }
  /* on small screens, keep portrait in flow and centered */
  .portrait-container { position: static; transform: none; margin: 18px 0 0; }
  .grid.cols-4 { grid-template-columns: repeat(2, 1fr); }
  .grid.cols-3 { grid-template-columns: repeat(1, 1fr); }
  .grid.cols-2 { grid-template-columns: repeat(1, 1fr); }
}

.card {
  border: 1px solid var(--border);
  border-radius: var(--radius);
  background: linear-gradient(180deg, rgba(255,255,255,0.06), rgba(255,255,255,0.02));
  padding: 18px;
  transition: transform 0.18s ease, box-shadow 0.18s ease, background 0.18s ease;
}
.card:hover {
  transform: translateY(-3px);
  box-shadow: 0 14px 40px rgba(0,0,0,0.28);
  background: linear-gradient(180deg, rgba(255,255,255,0.08), rgba(255,255,255,0.03));
}
.card h3 { margin: 0 0 8px; font-size: 18px; }
.card .meta { color: var(--muted); font-size: 14px; margin-bottom: 10px; }
.chip {
  display: inline-flex; align-items: center; gap: 6px;
  padding: 6px 10px; border-radius: 999px; border: 1px solid var(--accent-soft);
  background: rgba(255,255,255,0.04); color: #d6f3ff;
  font-size: 12px; margin: 6px 6px 0 0;
}

/* Skills section - icon grid */
.skills-grid {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 8px;
  margin-top: 8px;
}
.skill-tile {
  display: flex; align-items: center; gap: 8px;
  padding: 6px 8px; border-radius: 10px;
  background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01));
  border: 1px solid rgba(255,255,255,0.03);
  transition: transform 0.18s ease, box-shadow 0.18s ease, background 0.12s ease;
}
.skill-tile:hover { transform: translateY(-6px); box-shadow: 0 14px 30px rgba(0,0,0,0.28); }
.skill-icon { width: 36px; height: 24px; object-fit: cover; border-radius: 8px; display: inline-block; }
.skill-name { font-weight: 700; font-size: 14px; }

@media (max-width: 960px) {
  .skills-grid { grid-template-columns: repeat(2, minmax(0, 1fr)); }
}
@media (max-width: 520px) {
  .skills-grid { grid-template-columns: repeat(1, minmax(0, 1fr)); }
}

/* Project card image styling */
.project-image {
  width: 100%;
  /* fixed responsive image height so all project previews match (like PoultryPro) */
  height: clamp(160px, 26vh, 320px);
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 12px;
}

/* Increase spacing above Projects to separate it from Key Skills */
section#projects {
  /* reduced top spacing so Projects section is more compact */
  padding-top: clamp(40px, 6.5vh, 88px);
  padding-bottom: clamp(18px, 3vh, 36px);
}

/* Details / toggles */
details {
  background: rgba(255,255,255,0.04);
  border: 1px solid var(--border);
  border-radius: 12px; padding: 12px 14px;
}
details summary {
  cursor: pointer; list-style: none; font-weight: 700;
}
details[open] { background: rgba(255,255,255,0.06); }

/* Timeline */
.timeline { position: relative; padding-left: 22px; }
.timeline::before {
  content: ""; position: absolute; left: 8px; top: 0; bottom: 0;
  width: 2px; background: linear-gradient(var(--accent), transparent);
  opacity: 0.55; border-radius: 2px;
}
.t-item { position: relative; margin-bottom: 20px; }
.t-item::before {
  /* hide the timeline dot (was a decorative blue circle) */
  display: none;
}
.t-item h3 { margin: 0 0 6px; }
.t-item .when { color: var(--muted); font-size: 14px; }

/* Reveal animations */
.reveal {
  opacity: 0; transform: translateY(16px);
  transition: opacity 0.6s cubic-bezier(.2,.7,.3,1), transform 0.6s cubic-bezier(.2,.7,.3,1);
}
.reveal.inview { opacity: 1; transform: translateY(0); }

/* Footer */
footer {
  /* reduced vertical padding to make footer shorter */
  padding: 18px 0 28px;
  color: var(--muted);
  border-top: 1px solid var(--border);
  background: linear-gradient(180deg, transparent, rgba(0,0,0,0.25));
}
.footer-grid { display: grid; grid-template-columns: 1fr; gap: 16px; align-items: center; }
@media (max-width: 720px) { .footer-grid { grid-template-columns: 1fr; } }

/* Center footer content when there's only a single column (or to center items inside their grid cells) */
.footer-grid > div { justify-self: center; text-align: center; }

/* ensure the logo/brand inside the footer stays compact */
.brand { gap: 10px; }
.brand .logo { width: 44px; height: 44px; border-radius: 10px; font-size: 18px; }

/* Back-to-top */
.to-top {
  position: fixed; right: 16px; bottom: 16px; z-index: 60;
  opacity: 0; pointer-events: none; transform: translateY(10px);
  transition: all 0.2s ease;
}
.to-top.show { opacity: 1; pointer-events: auto; transform: translateY(0); }

/* Link accent */
a.link { color: #dff2ff; border-bottom: 1px dashed var(--accent-soft); text-decoration: none; }
a.link:hover { border-bottom-style: solid; }

/* Small icons (inline) */
.i { display: inline-block; width: 16px; height: 16px; border-radius: 4px;
  background: linear-gradient(135deg, var(--accent), var(--accent-2)); margin-right: 6px; vertical-align: -3px; }

/* Accessibility helpers */
.sr-only { position: absolute; left: -9999px; width: 1px; height: 1px; overflow: hidden; }

/* Additional responsive tweaks */
@media (max-width: 720px) {
  .wrap { padding: 0 12px; }
  section { padding: 48px 0; scroll-margin-top: 68px; }
  .hero { min-height: 70vh; padding-bottom: clamp(90px, 12vh, 140px); }
  .project-image { height: auto; max-height: 240px; }
  .nav-row { height: 56px; }
}

@media (max-width: 520px) {
  .wrap { padding: 0 6px; }
  section { padding: 36px 0; scroll-margin-top: 64px; }
  .hero { min-height: 60vh; padding-bottom: 80px; }
  .portrait-wrapper { width: clamp(100px, 36vw, 160px); height: clamp(100px, 36vw, 160px); }
  .skill-chip, .marquee-item { font-size: 12px; padding: 6px 8px; }
  nav ul { gap: 12px; }
  nav a { padding: 6px 8px; }
}

/* Very small screens polish (phones) */
@media (max-width: 420px) {
  header.nav .brand { left: 8px; }
  header.nav .brand .logo { width: 32px; height: 32px; }
  header.nav .wrap { padding-left: 8px; padding-right: 48px; }
  header.nav .brand div > div { font-size: 13px; max-width: 120px; white-space: nowrap; overflow: hidden; text-overflow: ellipsis; }

  .hero > .wrap { display: grid; grid-template-columns: 1fr; gap: 12px; align-items: start; padding: 12px; }
  .hero { padding-top: 28px; padding-bottom: 80px; min-height: 64vh; }
  .title { font-size: clamp(24px, 6.4vw, 36px); line-height: 1.05; }
  .sub, .tagline { font-size: 14px; }

  /* CTAs: left aligned, smaller */
  .cta { justify-content: flex-start; gap: 8px; }
  .btn { padding: 8px 10px; font-size: 14px; }

  /* Social links stacked left to avoid overlap */
  .hero-socials { justify-content: flex-start; gap: 8px; margin-top: 6px; }
  .social-link { padding: 6px 8px; border-radius: 8px; }

  /* hide marquee on very small devices to avoid crowding */
  .skill-marquee { display: none; }

  /* portrait sits below content and overlaps slightly for the visual shown */
  .portrait-wrapper { margin: 6px auto 0; width: clamp(100px, 34vw, 140px); height: clamp(100px, 34vw, 140px); border-width: 5px; }

  /* ensure project images don't overflow */
  .project-image { height: auto; max-height: 220px; }
}

/* Ensure grids collapse predictably on narrow screens */
@media (max-width: 960px) {
  .grid.cols-4 { grid-template-columns: repeat(2, 1fr); }
  .grid.cols-3 { grid-template-columns: repeat(1, 1fr); }
  .grid.cols-2 { grid-template-columns: repeat(1, 1fr); }
}

/* Prevent long text or overflow inside cards on small screens */
.card p, .card h3 { word-break: break-word; }
